html {
	height: 100%;
}

body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: url(/facify/static/media/main_background.1548f2fe.svg);
	background-size: cover;
	background-repeat: no-repeat;
	text-align: center;
	padding-bottom: 10px;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
	max-width: 800px;
	padding: 0 20px;
	margin: auto;
}

/* border-color: transparent #eaeaea transparent transparent; */
main {
	background: #ffffff;
	margin-bottom: 50px;
}

.stats {
	position: relative;
}
.info-demo .stats {
	display: none;
}

.input-file--hidden {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.label-input-file {
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	cursor: pointer;
	transition: 500ms;
	border-right: 5px solid #eaeaea;
}

.label-input-file:hover,
.input-file--hidden:focus + .label-input-file {
	outline: none;
	background: #c6c6c6;
	border-right: 5px solid #c6c6c6;
	color: #414141;
}

.input-button {
	border: none;
	padding: 10px 20px;
	color: #d1d1d1;
	position: relative;
	background: #000066;
	font-size: 1rem;
	cursor: pointer;
	transition: 500ms;
}

.input-button:hover {
	color: #ffffff;
	background: #000000;
}

.input-button:focus {
	outline: none;
	color: white;
	background: #000000;
}

.input-text {
	padding: 10px;
	background: #ffffff;
	border: none;
	color: #8e8e8e;
	font-weight: normal;
}

.input-text:focus {
	outline: none;
	color: #333333;
	font-weight: bold;
}

.input-text:focus::-webkit-input-placeholder {
	outline: none;
	color: #333333;
	font-weight: bold;
}

.input-text:focus::-ms-input-placeholder {
	outline: none;
	color: #333333;
	font-weight: bold;
}

.input-text:focus::placeholder {
	outline: none;
	color: #333333;
	font-weight: bold;
}

.input-group {
	display: grid;
	width: 100.05%;
	grid-template-columns: 1fr;
}

.multifile-upload-group {
	display: grid;
	grid-template-columns: 1fr 102px;
}
#logo {
	color: white;
	font-weight: bold;
	font-size: 2rem;
	margin: 50px 0;
}

.error-message {
	padding: 20px;
	color: #999999;
	font-size: 1rem;
}

@media (min-width: 375px) {
	.multifile-upload-group {
		grid-template-columns: 1fr 130px;
	}
}

@media (min-width: 600px) {
	.multifile-upload-group {
		grid-template-columns: 1fr 1fr;
	}
}

@media (min-width: 768px) {
	.App {
		padding: 0 50px;
	}
}

@media (min-width: 1200px) {
	.App {
		max-width: 80%;
	}

	.stats {
		display: none !important;
	}
	.info-demo .stats {
		display: block !important;
		grid-column: 1/-1;
	}
}

.bounding-box {
	position: absolute;
	box-shadow: inset 0 0 0 1px #14bff2;
	z-index: 1;
}

.container--image-demo {
	position: relative;
}

.container {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}
.image-demo {
	display: block;
	box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.25);
	width: 100%;
}

.image-panel {
	max-width: 800px;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: auto;
	grid-row-gap: 10px;
	margin: 0 auto;
}

.info-demo {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	justify-items: center;
	grid-row-gap: 10px;
	overflow-y: auto;
	height: 130px;
}

canvas {
	display: block;
}

/* @media (min-height: 600px) {
	.info-demo {
		grid-template-rows: repeat(5, 1fr);
	}
} */

@media (min-width: 375px) and (min-height: 800px) {
	.info-demo {
		grid-template-columns: repeat(5, 1fr);
		height: 300px;
	}
}

@media (min-width: 600px) {
	.info-demo {
		grid-template-columns: repeat(7, 1fr);
		grid-template-rows: repeat(5, 1fr);
	}
	.input-group {
		display: grid;
		grid-template-columns: 2fr 1fr;
	}
}

@media (min-width: 768px) {
	.info-demo {
		grid-template-columns: repeat(9, 1fr);
	}
}

@media (min-width: 1200px) {
	.image-panel {
		max-width: 100%;
		grid-template-columns: 3fr 2fr;
		padding-bottom: 30px;
	}
	.info-demo {
		grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
		grid-template-rows: 30px repeat(auto-fill, minmax(70px, 1fr));
		height: 100%;
	}
}

.fulfilling-square-spinner,
.fulfilling-square-spinner * {
	box-sizing: border-box;
}

.fulfilling-square-spinner {
	height: 100px;
	width: 100px;
	position: relative;
	border: 4px solid #9191915c;
	-webkit-animation: fulfilling-square-spinner-animation 4s infinite ease;
	        animation: fulfilling-square-spinner-animation 4s infinite ease;
}

.fulfilling-square-spinner .spinner-inner {
	vertical-align: top;
	display: inline-block;
	background-color: #9191915c;
	width: 100%;
	opacity: 1;
	-webkit-animation: fulfilling-square-spinner-inner-animation 4s infinite ease-in;
	        animation: fulfilling-square-spinner-inner-animation 4s infinite ease-in;
}

.container-loader {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	margin-top: 100px;
	margin-bottom: 50px;
}

h6,
h4 {
	color: #919191;
}

@-webkit-keyframes fulfilling-square-spinner-animation {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}

	25% {
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
	}

	50% {
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
	}

	75% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}

	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes fulfilling-square-spinner-animation {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}

	25% {
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
	}

	50% {
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
	}

	75% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}

	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@-webkit-keyframes fulfilling-square-spinner-inner-animation {
	0% {
		height: 0%;
	}

	25% {
		height: 0%;
	}

	50% {
		height: 100%;
	}

	75% {
		height: 100%;
	}

	100% {
		height: 0%;
	}
}

@keyframes fulfilling-square-spinner-inner-animation {
	0% {
		height: 0%;
	}

	25% {
		height: 0%;
	}

	50% {
		height: 100%;
	}

	75% {
		height: 100%;
	}

	100% {
		height: 0%;
	}
}

.swapping-squares-spinner,
.swapping-squares-spinner * {
	box-sizing: border-box;
}

.swapping-squares-spinner {
	height: 65px;
	width: 65px;
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-transform: scale(1.5);
	        transform: scale(1.5);
}

.swapping-squares-spinner .square {
	height: calc(65px * 0.25 / 1.3);
	width: calc(65px * 0.25 / 1.3);
	-webkit-animation-duration: 1000ms;
	        animation-duration: 1000ms;
	border: calc(65px * 0.04 / 1.3) solid #224aff;
	margin-right: auto;
	margin-left: auto;
	position: absolute;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
}

.swapping-squares-spinner .square:nth-child(1) {
	-webkit-animation-name: swapping-squares-animation-child-1;
	        animation-name: swapping-squares-animation-child-1;
	-webkit-animation-delay: 500ms;
	        animation-delay: 500ms;
}

.swapping-squares-spinner .square:nth-child(2) {
	-webkit-animation-name: swapping-squares-animation-child-2;
	        animation-name: swapping-squares-animation-child-2;
	-webkit-animation-delay: 0ms;
	        animation-delay: 0ms;
}

.swapping-squares-spinner .square:nth-child(3) {
	-webkit-animation-name: swapping-squares-animation-child-3;
	        animation-name: swapping-squares-animation-child-3;
	-webkit-animation-delay: 500ms;
	        animation-delay: 500ms;
}

.swapping-squares-spinner .square:nth-child(4) {
	-webkit-animation-name: swapping-squares-animation-child-4;
	        animation-name: swapping-squares-animation-child-4;
	-webkit-animation-delay: 0ms;
	        animation-delay: 0ms;
}

@-webkit-keyframes swapping-squares-animation-child-1 {
	50% {
		-webkit-transform: translate(150%, 150%) scale(2, 2);
		        transform: translate(150%, 150%) scale(2, 2);
	}
}

@keyframes swapping-squares-animation-child-1 {
	50% {
		-webkit-transform: translate(150%, 150%) scale(2, 2);
		        transform: translate(150%, 150%) scale(2, 2);
	}
}

@-webkit-keyframes swapping-squares-animation-child-2 {
	50% {
		-webkit-transform: translate(-150%, 150%) scale(2, 2);
		        transform: translate(-150%, 150%) scale(2, 2);
	}
}

@keyframes swapping-squares-animation-child-2 {
	50% {
		-webkit-transform: translate(-150%, 150%) scale(2, 2);
		        transform: translate(-150%, 150%) scale(2, 2);
	}
}

@-webkit-keyframes swapping-squares-animation-child-3 {
	50% {
		-webkit-transform: translate(-150%, -150%) scale(2, 2);
		        transform: translate(-150%, -150%) scale(2, 2);
	}
}

@keyframes swapping-squares-animation-child-3 {
	50% {
		-webkit-transform: translate(-150%, -150%) scale(2, 2);
		        transform: translate(-150%, -150%) scale(2, 2);
	}
}

@-webkit-keyframes swapping-squares-animation-child-4 {
	50% {
		-webkit-transform: translate(150%, -150%) scale(2, 2);
		        transform: translate(150%, -150%) scale(2, 2);
	}
}

@keyframes swapping-squares-animation-child-4 {
	50% {
		-webkit-transform: translate(150%, -150%) scale(2, 2);
		        transform: translate(150%, -150%) scale(2, 2);
	}
}

